
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 1;
  }
  
  .modal {
    border: none;
    border-radius: 6px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0);
    padding: 0;
    overflow: hidden;
    z-index: 1;
    height: 650px; 
   
    overflow-y: scroll; 
    top: 10%;
    
  }
  .button-container {
    display: flex;
    justify-content: flex-end; 
    align-items: flex-start;
  }
  .icon-gap {
    width: 10px; 
  }
  .icon-wrapper {
    border: 2px solid #000; 
    border-radius: 8px; 
    padding: 4px; 
    display: inline-flex; 
    align-items: center; 
  }